export class ModeItem {
    private static modes = [
        ['E-Mail', 'Почта'],
        ['Phone Call', 'Телефонный звонок'],
        ['telegram', 'Телеграм'],
        ['viber', 'Вайбер'],
        ['vk', 'Вк'],
        ['Web Form', 'Веб форма'],
        ['Не назначен', 'Не назначен']
    ];

    value: string;
    label: string;
    checked: boolean;

    constructor(value: any, label: any, checked?: boolean) {
        this.value = value;
        this.label = label;
        this.checked = checked ? checked : false;
    }

    public static getModes(): Array<ModeItem> {
        const result = new Array<ModeItem>();
        this.modes.forEach(arr =>
            result.push(new ModeItem(arr[0], arr[1])));
        return result;
    }
}
